<template>
  <div id="edit-page">
    <GlobalHeader />
    <GlobalMessage />
    <!-- メインエリア -->
    <b-container class="mt-3">
      <h2>図面精査内容編集</h2>
    </b-container>
    <b-container class="mb-5">
      <b-form v-on:submit.prevent="submitSave">
      <b-container class="border">
        <h5 class="mt-3">製品情報</h5>
        <b-row>
          <b-col>
            <label class="font-weight-bold">品名</label>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col>
            <b-form-input type="text" v-model="child_name" />
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">図番</label>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col>
            <b-form-input type="text" v-model="child_figure_number" />
          </b-col>
        </b-row>
      </b-container>
      <b-container class="border mt-5">
        <h5 class="mt-3">材料</h5>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材料種類</label>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col cols="4" md="3">
            <b-form-select v-model="material_big" :options="material_big_options">
            </b-form-select>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材料ID</label>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col cols="4" md="3">
            <v-autocomplete
              dense
              v-model="material_id"
              :items="material_list"
              item-text="material_id"
              @change="changeMaterial"></v-autocomplete>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材料形状</label>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="material_shape" />
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材質</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="material_sml_txt" />
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">記号</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="material_symbol" />
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">硬度・仕上げ</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="hardness" />
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">板厚</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="sheet_thickness" @change="changePieceSeat" />
          </b-col>
          <b-col cols="1">
            <p>μm</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材料サイズW</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="material_size_w" @change="changePieceSeat" />
          </b-col>
          <b-col cols="1">
            <p>mm</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">材料サイズL</label>
          </b-col> 
        </b-row>
        <b-row class="mb-3">
          <b-col cols="5" md="4">
            <b-form-input type="text" v-model="material_size_l" @change="changePieceSeat" />
          </b-col>
          <b-col cols="1">
            <p>mm</p>
          </b-col>
        </b-row>
      </b-container>
      <b-container class="border mt-5">
        <h5 class="mt-3">面付け数</h5>
        <b-row>
          <b-col>
            <label class="font-weight-bold">製品サイズ</label>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            X方向
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="4" md="3">
            <b-form-input
              type="text"
              v-model="size_x"
              @change="changePieceSeat"
            />
          </b-col>
          <b-col cols="1">
            <p>mm</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            Y方向
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col cols="4" md="3">
            <b-form-input
              type="text"
              v-model="size_y"
              @change="changePieceSeat"
            />
          </b-col>
          <b-col cols="1">
            <p>mm</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label class="font-weight-bold">面付け数</label>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="4" md="3">
            <b-form-input
              type="number"
              v-model="imposition_amount_piece_seat"
            />
          </b-col>
          <b-col cols="4" md="3">
            <p>個/シート</p>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="4" md="3">
            <b-form-input
              type="number"
              v-model="imposition_amount_piece_flame"
            />
          </b-col>
          <b-col cols="4" md="3">
            <p>個/フレーム</p>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col cols="4" md="3">
            <b-form-input
              type="number"
              v-model="imposition_amount_flame_seat"
            />
          </b-col>
          <b-col cols="4" md="3">
            <p>フレーム/シート</p>
          </b-col>
        </b-row>
      </b-container>
      <b-container class="border mt-5">
        <h5 class="mt-3">確認事項</h5>
        <b-form-textarea
          type="text"
          class="mb-3"
          rows="5"
          v-model="fixes"
        ></b-form-textarea>
      </b-container>
        <div class="row text-center mt-5">
          <div class="col-sm-12">
            <router-link
              :to="{
                  name: 'child',
                  params: {
                    parent_id: parent_id,
                    child_id: child_id
                  }
                }"
            >
              <b-button variant="light" class="mx-1">戻る</b-button>
            </router-link>
            <b-button type="submit" variant="primary" class="mx-1">
              送信
            </b-button>
          </div>
        </div>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import api from "@/services/api"
import GlobalHeader from "@/components/GlobalHeader.vue"
import GlobalMessage from "@/components/GlobalMessage.vue"

export default {
  components: {
    GlobalHeader,
    GlobalMessage
  },
  props: {
    parent_id: {
      required: true
    },
    child_id: {
      required: true
    }
  },
  data() {
    return {
      // 入力フォームの内容
      // process: null,
      // process_options: [
      //   { value: null, text: "----"},
      //   { value: 0, text: "通常工程"},
      //   { value: 1, text: "2工程"},
      //   { value: 2, text: "3工程"},
      //   { value: 3, text: "4工程"}
      // ],
      // special_process: null,
      // after_process: null,
      // outsourcing: null,
      // juction: null,
      // juction_options: [
      //   { value: null, text: "----"},
      //   { value: 0, text: "社内"},
      //   { value: 1, text: "外注"}
      // ],
      // laminate_configuration: null,
      child_name: null,
      child_figure_number: null,
      material_big: null,
      material_big_options: [
        { value: null, text: "----"},
        { value: 0, text: "自社材"},
        { value: 1, text: "購入材"},
        { value: 2, text: "支給材"}
      ],
      material_list: [],
      material_dict: {},
      material_id: null,
      material_shape: null,
      material_sml_txt: null,
      material_symbol: null,
      hardness: null,
      sheet_thickness: null,
      material_size_w: null,
      material_size_l: null,
      imposition_amount_piece_seat: null,
      imposition_amount_piece_flame: null,
      imposition_amount_flame_seat: null,
      fixes: null,
      // drop_technique: null,
      // correction: null,
      // without_fee: null,
      // face_interval_direction_x: null,
      // face_interval_direction_y: null,
      // imposition_amount_direction_x: null,
      // imposition_amount_direction_y: null,
      // production_checklist: null,
      //計算用
      size_x: null,
      size_y: null,
      effect_size_w: null,
      effect_size_l: null,
      correction: null,
      without_fee: null,
      between_products: null,
      size_x_2: null,
      size_y_2: null,
      imposition_amount_direction_x: null,
      imposition_amount_direction_y: null,
      sheet_thickness_mm: null,
    }
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username
    }
  },
  methods: {
    changePieceSeat() {
      if (this.material_size_w && this.material_size_l && this.sheet_thickness && this.size_x && this.size_y) {
        this.effect_size_w = Number(this.material_size_w) - 20
        this.effect_size_l = Number(this.material_size_l) - 30
        this.sheet_thickness_mm = Number(this.sheet_thickness) * 0.001
        if (Number(this.sheet_thickness_mm) <= 0.06) {
          this.correction = 0.01
        }
        else if (Number(this.sheet_thickness_mm) > 0.06 && Number(this.sheet_thickness_mm) <= 0.5) {
          this.correction = Number(this.sheet_thickness_mm) * 0.25 - 0.005
        }
        else {
          this.correction = Number(this.sheet_thickness_mm) * 0.15
        }
        if (Number(this.sheet_thickness_mm) > 0.3) {
          this.without_fee = this.sheet_thickness_mm
        }
        else {
          this.without_fee = 0.3
        }
        if (Number(this.sheet_thickness_mm) < 0.5) {
          this.between_products = 1
        }
        else {
          this.between_products = Number(this.sheet_thickness_mm) * 2
        }
        if (Number(this.sheet_thickness_mm) < 0.2) {
          this.size_x_2 = Number(this.size_x) + Number(this.sheet_thickness_mm) * 2 + Number(this.correction) *2 + 0.3 + Number(this.between_products)
        }
        else {
          this.size_x_2 = Number(this.size_x) + Number(this.sheet_thickness_mm) * 2 + Number(this.correction) *2 + Number(this.without_fee) + Number(this.between_products)
        }
        if (Number(this.sheet_thickness) < 200) {
          this.size_y_2 = Number(this.size_y) + Number(this.sheet_thickness_mm) * 2 + Number(this.correction) *2 + 0.3 + Number(this.between_products)
        }
        else {
          this.size_y_2 = Number(this.size_y) + Number(this.sheet_thickness_mm) * 2 + Number(this.correction) *2 + Number(this.without_fee) + Number(this.between_products)
        }
        this.imposition_amount_direction_x = Math.floor(Number(this.effect_size_w) / Number(this.size_x_2))
        this.imposition_amount_direction_y = Math.floor(Number(this.effect_size_l) / Number(this.size_y_2))
        this.imposition_amount_piece_seat = Number(this.imposition_amount_direction_x) * Number(this.imposition_amount_direction_y)
      }
      else {
        this.imposition_amount_piece_seat = null
      }
    },
    createDict: function() {
      // 材料情報取得
      api({
        method: "GET",
        url: "/materials/"
      }).then(response => {
        this.material_list = response.data;
        for (let i = 0; i < response.data.length; i++) {
          this.material_dict[response.data[i].material_id] = [response.data[i].material_shape, response.data[i].material_sml_txt, response.data[i].material_symbol, response.data[i].hardness, response.data[i].sheet_thickness, response.data[i].material_size_w, response.data[i].material_size_l]
        }
      })
    },
    changeMaterial() {
      if (this.material_id in this.material_dict) {
        this.material_shape = this.material_dict[this.material_id][0]
        this.material_sml_txt = this.material_dict[this.material_id][1]
        this.material_symbol = this.material_dict[this.material_id][2]
        this.hardness = this.material_dict[this.material_id][3]
        this.sheet_thickness = this.material_dict[this.material_id][4]
        this.material_size_w = this.material_dict[this.material_id][5]
        this.material_size_l = this.material_dict[this.material_id][6]
      } else {
        this.material_shape = null
        this.material_sml_txt = null
        this.material_symbol = null
        this.hardness = null
        this.sheet_thickness = null
        this.material_size_w = null
        this.material_size_l = null
      }
      this.changePieceSeat()
    },
    // 編集処理
    submitSave: function() {
      if (this.size_x == "") {
        this.size_x = null
      }
      if (this.size_y == "") {
        this.size_y = null
      }
      if (this.imposition_amount_piece_seat == "") {
        this.imposition_amount_piece_seat = null
      }
      if (this.imposition_amount_piece_flame == "") {
        this.imposition_amount_piece_flame = null
      }
      if (this.imposition_amount_flame_seat == "") {
        this.imposition_amount_flame_seat = null
      }
      api({
        method: "PATCH",
        url: `/children/${this.child_id}/`,
        data: {
          // child_id: this.child_id,
          // parent_id: this.parent_id,
          // process: this.process,
          // special_process: this.special_process,
          // after_process: this.after_process,
          // outsourcing: this.outsourcing,
          // juction: this.juction,
          // laminate_configuration: this.laminate_configuration,
          child_name: this.child_name,
          child_figure_number: this.child_figure_number,
          material_big: this.material_big,
          material_id: this.material_id,
          material_shape: this.material_shape,
          material_sml_txt: this.material_sml_txt,
          material_symbol: this.material_symbol,
          hardness: this.hardness,
          sheet_thickness: this.sheet_thickness,
          material_size_w: this.material_size_w,
          material_size_l: this.material_size_l,
          imposition_amount_piece_seat: this.imposition_amount_piece_seat,
          imposition_amount_piece_flame: this.imposition_amount_piece_flame,
          imposition_amount_flame_seat: this.imposition_amount_flame_seat,
          fixes: this.fixes,
          size_x: this.size_x,
          size_y: this.size_y,
          // drop_technique: this.drop_technique,
          // correction: this.correction,
          // without_fee: this.without_fee,
          // face_interval_direction_x: this.face_interval_direction_x,
          // face_interval_direction_y: this.face_interval_direction_y,
          // imposition_amount_direction_x: this.imposition_amount_direction_x,
          // imposition_amount_direction_y: this.imposition_amount_direction_y,
          // production_checklist: this.production_checklist,
        }
      }).then(response => {
        this.$router.push({
          name: "child",
          params: {
            parent_id: response.data.parent_id,
            child_id: response.data.child_id
          }
        });
      });
    }
  },
  // フォームに初期値をセット
  async beforeRouteEnter(to, from, next) {
    let endpoint = `/children/${to.params.child_id}/`
    let data = await api({ url: endpoint })
    return next(vm => {
      // (vm.parent_id = data.data.parent_id),
      // (vm.process = data.data.process),
      // (vm.special_process = data.data.special_process),
      // (vm.after_process = data.data.after_process),
      // (vm.outsourcing = data.data.outsourcing),
      // (vm.juction = data.data.juction),
      // (vm.laminate_configuration = data.data.laminate_configuration),
      (vm.child_name = data.data.child_name),
      (vm.child_figure_number = data.data.child_figure_number),
      (vm.material_big = data.data.material_big),
      (vm.material_id = data.data.material_id),
      (vm.material_shape = data.data.material_shape),
      (vm.material_sml_txt = data.data.material_sml_txt),
      (vm.material_symbol = data.data.material_symbol),
      (vm.hardness = data.data.hardness),
      (vm.sheet_thickness = data.data.sheet_thickness),
      (vm.material_size_w = data.data.material_size_w),
      (vm.material_size_l = data.data.material_size_l),
      (vm.imposition_amount_piece_seat =
        data.data.imposition_amount_piece_seat),
      (vm.imposition_amount_piece_flame =
        data.data.imposition_amount_piece_flame),
      (vm.imposition_amount_flame_seat =
        data.data.imposition_amount_flame_seat),
      (vm.fixes = data.data.fixes),
      (vm.size_x = data.data.size_x),
      (vm.size_y = data.data.size_y)
      // (vm.drop_technique = data.data.drop_technique),
      // (vm.correction = data.data.correction),
      // (vm.without_fee = data.data.without_fee),
      // (vm.face_interval_direction_x = data.data.face_interval_direction_x),
      // (vm.face_interval_direction_y = data.data.face_interval_direction_y),
      // (vm.imposition_amount_direction_x =
      //   data.data.imposition_amount_direction_x),
      // (vm.imposition_amount_direction_y =
      //   data.data.imposition_amount_direction_y),
      // (vm.production_checklist = data.data.production_checklist),
    })
  },
  created() {
    this.createDict()
  }
};
</script>
