<template>
  <div id="edit-page">
    <GlobalHeader />
    <GlobalMessage />
    <!-- メインエリア -->
    <b-container class="my-3">
      <h2>接合品登録</h2>
      <b-container>
        <b-form v-on:submit.prevent="submitSave">
          <b-row>
            <b-col>
              <label class="font-weight-bold">品名</label>
            </b-col>
          </b-row>
          <b-row class="mb-3">
            <b-col>
              <b-form-input type="text" v-model="assy_name" />
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <label class="font-weight-bold">中間品/完成品</label>
            </b-col> 
          </b-row>
          <b-row class="mb-3">
            <b-col>
              <b-form-radio-group
                  v-model="matl_type"
                  :options="matl_type_options"
                  stacked
                ></b-form-radio-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col class="border">品名</b-col>
            <b-col class="border">枚数</b-col>
          </b-row>
          <b-row v-for="child in number" :key="child.child_id">
            <b-col class="border">{{ child.child_name }}</b-col>
            <b-col class="border">
              <b-form-input
                type="number"
                v-model="child.number"
              />
            </b-col>
          </b-row>
          <!-- <b-row>
            <b-col>
              <label class="font-weight-bold">仕入先</label>
            </b-col>
          </b-row>
          <b-row class="mb-3">
            <b-col cols="9" md="6">
              <v-autocomplete
                v-model="company_id"
                :items="company_list"
                item-text="company_name"
                item-value="company_id"
              ></v-autocomplete>
            </b-col>
          </b-row> -->
          <div class="row text-center mt-5">
            <div class="col-sm-12">
              <router-link
                :to="{
                    name: 'parent',
                    params: {
                      parent_id: parent_id
                    }
                  }"
              >
                <b-button variant="light" class="mx-1">戻る</b-button>
              </router-link>
              <b-button type="submit" variant="primary" class="mx-1">
                送信
              </b-button>
            </div>
          </div>
        </b-form>
      </b-container>
    </b-container>
    {{ children }}
    {{ number }}
  </div>
</template>

<script>
import api from "@/services/api"
import GlobalHeader from "@/components/GlobalHeader.vue"
import GlobalMessage from "@/components/GlobalMessage.vue"

export default {
  components: {
    GlobalHeader,
    GlobalMessage
  },
  props: {
    parent_id: {
      required: true
    }
  },
  data() {
    return {
      // 入力フォームの内容
      assy_name: null,
      assys: null,
      // children: null,
      matl_type: null,
      matl_type_options: [
        { text: '中間品', value: '0'},
        { text: '完成品', value: '1'}
      ],
      sheets: null,
      number: []
    }
  },
  methods: {
    getChildren: function() {
      api({
        method: "GET",
        url: `/parents/${this.parent_id}/children/`
      }).then(response => {
        // this.children = response.data
        for (let i = 0; i < response.data.length; i++) {
          this.number.push({ "child_id": response.data[i].child_id, "child_name": response.data[i].child_name, number: ""})
        }
      })
    },
    submitSave: function() {
      var promise_ary = []
      api({
        method: "POST",
        url: "/assys/",
        data: {
          assy_name: this.assy_name,
          parent_id: this.parent_id,
          matl_type: this.matl_type
        }
      }).then(response => {
        for (let i = 0; i < this.number.length; i++) {
          if (this.number[i].number) {
            var promise = api({
              method: "POST",
              url: "/assys_children/",
              data: {
                assy_id: response.data.assy_id,
                child_id: this.number[i].child_id,
                sheets: this.number[i].number
              }
            })
            promise_ary.push(promise)
          }
        }
        // console.log(response)
        // this.$router.go({
        //   name: "assy_editor",
        //   params: {
        //     parent_id: response.data.parent_id
        //   }
        // })
      })
      Promise.all(promise_ary).then(() => {
        this.$router.push({
          name: "parent",
          params: {
            parent_id: this.parent_id
          }
        })
      })
    },
    // getPart: function() {
    //   api({
    //     method: "GET",
    //     url: `/parents/${this.parent_id}/parts/`
    //   }).then(response => {
    //     this.parts = response.data
    //   })
    // },
    // deletePart: function(part_id) {
    //   api({
    //     method: "DELETE",
    //     url: `/parts/${part_id}/`
    //   }).then(
    //     this.$router.go({
    //       name: "part_editor",
    //       params: {
    //         parent_id: this.parent_id
    //       }
    //     })
    //   )
    // }
  },
  created() {
    this.getChildren()
    // this.getPart()
  }
}
</script>