<template>
  <div>
    <b-container class="mt-3">
      <b-row>
        <b-col cols="5" md="4"><h3>マスク</h3></b-col>
        <b-col v-show="this.seigi_bool">
          <router-link
            :to="{ name: 'mask_create', params: { parent_id: parent_id, child_id: child_id } }"
          >
            <b-button variant="primary" class="text-dark mx-1">新規登録</b-button>
          </router-link>
        </b-col>
      </b-row>
    </b-container>
    <b-container class="mb-5">
      <b-row>
        <b-col class="bg-dark border text-light">マスクID</b-col>
      </b-row>
      <b-row v-for="mask in masks" :key="mask.mask_id">
        <b-col class="border">
          <router-link
            :to="{ name: 'mask', params: { parent_id: parent_id, child_id: child_id, mask_id: mask.mask_id } }"
            >{{ mask.mask_id }}
          </router-link>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import api from "@/services/api"

// グループID
const SEIGI = 3; // 生技

export default {
  props: {
    parent_id: {
      required: true
    },
    child_id: {
      required: true
    }
  },
  data() {
    return {
      group: null,
      seigi_bool: null,
      masks: null,
    };
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username
    }
  },
  methods: {
    getData: function() {
      api({
        method: "GET",
        url: `/getusers2/${this.username}/`
      }).then(response =>{
        this.group = response.data[0].groups
        this.seigi_bool = this.group.includes(SEIGI)
      })
      api({
        method: "GET",
        url: `/children/${this.child_id}/masks/`,
      }).then(response => {
        this.masks = response.data
      })
    }
  },
  created() {
    this.getData()
  }
}
</script>
