<template>
  <div>
    <b-container fluid="xl" class="border my-5">
      <b-container>
        <b-row>
          <b-col cols="5" md="4"><h3>BOM</h3></b-col>
          <b-col v-show="this.seigi_bool">
            <router-link
              :to="{
                name: 'bom_editor',
                params: {
                  parent_id: parent_id
                }
              }"
            >
              <b-button variant="warning" class="text-dark mx-1">編集</b-button>
            </router-link>
          </b-col>
        </b-row>
        
      </b-container>
      
    </b-container>
  </div>
</template>
  
<script>
import api from "@/services/api"

export default {
  props: {
    parent_id: {
      required: true
    }
  },
  data() {
    return {
      group: null,
      seigi_bool: null,
      parent: null,
      SEIGI: 3
    }
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username
    }
  },
  methods: {
    getData: function() {
      api({
        method: "GET",
        url: `/getusers2/${this.username}/`
      }).then(response =>{
        this.group = response.data[0].groups
        this.seigi_bool = this.group.includes(this.SEIGI)
      })
      api({
        method: "GET",
        url: `/parents/${this.parent_id}/`
      }).then(response => {
        this.parent = response.data
      })
    } 
  },
  created() {
    this.getData()
  }
}
</script>