<template>
  <div id="manager-page">
    <b-container fluid="xl" class="border my-5" v-show="seigi_bool">
        <b-container class="my-3">
          <h4 class="mt-3">精査担当者</h4>
        </b-container>
        <b-container class="my-3">
          <b-row class="mt-5">
            <b-col>
              <b-form-radio-group
                v-model="manager"
                :options="seigi_list"
                stacked
              ></b-form-radio-group>
            </b-col>
          </b-row>
          <b-button
            @click="changeManager"
            class="text-light"
            variant="primary"
          >担当者決定</b-button>
        </b-container>
      </b-container>
  </div>
</template>
  
<script>
import api from "@/services/api";
import moment from "moment";

// グループID
const SEIGI = 3; // 生技

// ベルのメッセージナンバー
const MANAGER = 6; // 担当者決定
  
export default {
  props: ["parent_id"],
  data() {
    return {
      scrutinizer: null,
      seigi_bool: null,
      bell_id_list: [],
      seigi_list: [],
      manager: null,
    };
  },
  computed: {
    username: function() {
      return this.$store.state.auth.username;
    },
  },
  methods: {
    getData: function() {
      api({
        method: "GET",
        url: `/parents/${this.parent_id}/`
      }).then(response => {
        this.manager = response.data.scrutinizer
      })
      api({
        method: "GET",
        url: `/getusers2/${this.username}/`
      }).then(response =>{
        this.scrutinizer = response.data[0].id
        this.seigi_bool = response.data[0].groups.includes(SEIGI)
      })
      // 生技グループのリスト作成
      api({
        method: "GET",
        url: `/getusers/${SEIGI}/`
      }).then(response => {
        for (let i = 0; i < response.data.length; i++) {
          this.seigi_list.push({"value": response.data[i].id, "text": response.data[i].last_name + response.data[i].first_name})
        }
        for (let i = 0; i < this.seigi_list.length; i++) {
          api({
            method: "GET",
            url: `/getbells2/${this.seigi_list[i].value}/${this.parent_id}/`
          }).then(response =>{
            this.bell_id_list.push(response.data[0].id)
          })
        }
      })
    },
    // 担当者決定ボタンを押したときの処理
    changeManager: function() {
      api({
        method: "PATCH",
        url: `/parents/${this.parent_id}/`,
        data: {
          scrutinizer: this.manager
        }
      }).then(response => {
        for (let i = 0; i < this.bell_id_list.length; i++) {
          api({
            method: "PATCH",
            url: `/bells/${this.bell_id_list[i]}/`,
            data: {
              is_active: true,
              updated_at: moment().format(),
              updated_user: this.scrutinizer,
              message_number: MANAGER
            }
          })
        }
        this.$router.go({
          name: "parent",
          params: {
            parent_id: response.data.parent_id
          }
        })
      })
    }
  },
  created() {
    this.getData()
  }
};
</script>