<template>
  <div id="edit-page">
    <GlobalHeader />
    <GlobalMessage />
    <!-- メインエリア -->
    <b-container class="my-3">
      <h2>BOM</h2>
    </b-container>
    <b-button @click="create_bom">作成</b-button>
    <b-container>
      <b-form v-on:submit.prevent="submitSave">
        <b-container class="my-3">
          <b-row>
            <b-col class="border font-weight-bold">親品目</b-col>
            <b-col class="border font-weight-bold">作業</b-col>
            <b-col class="border font-weight-bold">子品目</b-col>
            <b-col class="border" cols="2"></b-col>
          </b-row>
          <b-row
            v-for="(bom, index) in boms"
            :key="index"
            :draggable="true"
            @dragstart="dragStart(index)"
            @dragenter="dragEnter(index)"
            @dragover.prevent
            @dragend="dragEnd"
            :class="index === dragIndex ? 'dragging' : ''"
          >
            <b-col class="border">{{ bom.parent_name }}</b-col>
            <b-col class="border">{{ bom.operation_name }}</b-col>
            <b-col class="border">{{ bom.child_name }}</b-col>
            <b-col class="border" cols="2">
              <b-button
                variant="danger"
                @click="deleteOperation(index)"
              >削除</b-button>
            </b-col>
          </b-row>
        </b-container>
        <b-container class="text-center my-5">
          <div class="col-sm-12">
            <router-link
              :to="{
                  name: 'child',
                  params: {
                    parent_id: parent_id,
                    child_id: child_id
                  }
                }"
            >
              <b-button variant="light" class="mx-1">戻る</b-button>
            </router-link>
            <b-button type="submit" variant="primary" class="mx-1">
              送信
            </b-button>
          </div>
        </b-container>
      </b-form>
    </b-container>
    {{ boms }}

  </div>
</template>

<script>
import api from "@/services/api"
import GlobalHeader from "@/components/GlobalHeader.vue"
import GlobalMessage from "@/components/GlobalMessage.vue"

export default {
  components: {
    GlobalHeader,
    GlobalMessage
  },
  props: {
    parent_id: {
      required: true
    },
    child_id: {
      required: true
    }
  },
  data() {
    return {
      // 入力フォームの内容
      boms: [],
      dragIndex: null,
      parent: null,
      children: null,
      operations_list: []
    }
  },
  methods: {
    submitSave: async function() {

    },
    getData: function() {
      api({
        method: "GET",
        url: `/parents/${this.parent_id}/`
      }).then(response => {
        this.parent = response.data
      })
      api({
        method: "GET",
        url: `/parents/${this.parent_id}/children/`
      }).then(response => {
        this.children = response.data
        for (let i = 0; i < this.children.length; i++) {
          api({
            method: "GET",
            url: `/get_children_operations3/${this.children[i].child_id}/`
          }).then(response2 => {
            this.operations_list.push(response2.data)
          })
        }
      })
    },
    dragStart: function(index) {
      this.dragIndex = index
    },
    dragEnter: function(index) {
      if (index === this.dragIndex) return
      const deleteElement = this.boms.splice(this.dragIndex, 1)[0]
      this.boms.splice(index, 0, deleteElement)
      this.dragIndex = index
    },
    dragEnd: function() {
      this.dragIndex = null
    },
    deleteOperation: function(index) {
      this.boms.splice(index, 1)
    },
    create_bom: function() {
      for (let i = 0; i < this.operations_list.length; i++) {
        for (let j = 0; j < this.operations_list[i].length; j++) {
          this.boms.push({
            'parent_id': this.operations_list[i][j].child_id,
            'parent_name': this.operations_list[i][j].child_name,
            'operation_id': this.operations_list[i][j].operation_id,
            'operation_name': this.operations_list[i][j].operation_name,
            'child_id': this.operations_list[i][j].child_id,
            'child_name': this.operations_list[i][j].child_name
          })
        }
      }
    }
  },
  created() {
    this.getData()
  }
}
</script>

<style>
.dragging {
  background-color: #eee;
}
</style>